import React from 'react';
import './AdminSocial.css';

export default function AdminSocial() {
    const adminDetails = [
        {
            link: 'LinkedIn',
            url : 'https://www.linkedin.com/in/athar-nadeem-01740996/'
        },
        {
            link: 'Email',
            url : 'athar.nadeemniftfc@gmail.com'      
        },
        {
            link: 'Behance',
            url : 'https://www.linkedin.com/in/athar-nadeem-01740996/'      
        }
    ]
  return (
    <React.Fragment>
        <div className='social-link-group'>
            <nav>
                {adminDetails.map((item, index) => {
                    return <a key={index} rel="noopener noreferrer" href={item.link === 'Email' ? `mailto:${item.url}` : item.url} target='_blank'>{item.link}</a>
                })}
            </nav>
        </div>
    </React.Fragment>
  )
}
