import React from 'react'
import './Navigation.css';

const Navigation = (props) => {
  const {navigations, filterFun, isNavActive} = props;
  // let index = 1;
  // let activeClass = (isNavActive ? 'active': '');
  // console.log(props);
  return (
    <React.Fragment>
      <nav className='nav-fliter'>
          <ul>
              {navigations.map(
                (item, index) => 
                <li className={isNavActive === index ? 'active': ''} key={ index } maplink={item.mapLink} onClick={filterFun.bind(this, index, item.mapLink)}>
                  {item.name}<span>{item.count}</span>
                </li>
              )}
          </ul>
        </nav>
    </React.Fragment>
  )
}

export default Navigation;