import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import Project from '../components/Project';

class Listing extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isNavActive: 0,
            projectItem: [
                {
                    title: 'Cleartrip',
                    category: 'Website',
                    mapLink: 'websites'
                },
                {
                    title: 'Google',
                    category: 'Mobile App',
                    mapLink: 'mobile-apps'
                },
                {
                    title: 'Amazon',
                    category: 'Digital Product',
                    mapLink: 'digi-products'
                },
                {
                    title: 'Facebook',
                    category: 'Website',
                    mapLink: 'websites'
                },
                {
                    title: 'Google',
                    category: 'Mobile App',
                    mapLink: 'mobile-apps'
                },
                {
                    title: 'Amazon',
                    category: 'Digital Product',
                    mapLink: 'digi-products'
                }
            ],
            navItems: [
                {
                    name: 'All',
                    mapLink: 'all',
                    count: '7',
                },
                {
                    name: 'Digital Products',
                    mapLink: 'digi-products',
                    count: '3',
                },
                {
                    name: 'Mobile Applications',
                    mapLink: 'mobile-apps',
                    count: '2',
                },
                {
                    name: 'Websites',
                    mapLink: 'websites',
                    count: '2',
                }
                
            ],
            filteredProject: []
        } 
        this.onClick = this.filterFun.bind(this);
    }
    filterFun = (navIndex, mapLink) => {
        const filterItem = this.state.projectItem.filter(item => {
            return item.mapLink === mapLink
        });
        if(navIndex === 0 || mapLink === 'all') {
            this.setState({
                isNavActive: navIndex,
                filteredProject: this.state.projectItem
            });
        }
        else {
            this.setState({
                isNavActive: navIndex,
                filteredProject: filterItem
            });
        }
    }
    componentDidMount = () => {
        const filteredProject = this.state.projectItem
        this.setState({
            filteredProject
        })
        // console.log(filteredProject)
    }
    render() {
        const {navItems, isNavActive} = this.state;
        return(
            <React.Fragment>
                <section className='project-list'>
                    <div className="container-min">
                        <Navigation navigations = {navItems} filterFun = {this.filterFun} isNavActive = {isNavActive} />
                        <Project filteredProject = {this.state.filteredProject} />
                    </div>
                </section>
            </React.Fragment>
        )
    }
}

export default Listing;

