import React, { Component } from 'react';
import './Project.css';

export default class Project extends Component {
  render() {
    const {filteredProject} = this.props;
    console.log(filteredProject);
    return (
      <React.Fragment>
        <div className='projectList-wrapper'>
            <div className='row'>
                {filteredProject.map((item, i) => {
                    return <div key={i} className='col-6' data-attr={item.mapLink}>
                            <div className='card'>
                                <figcaption>
                                  <h2>{item.title}</h2>
                                  <span className='category'>{item.category}</span>
                                </figcaption>
                            </div>
                          </div>
                })}

            </div>
        </div>
      </React.Fragment>
    )
  }
}
